import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
	routes: [
		{
			path: '/home',
			name: 'home',
			component: resolve => require(['@/components/home'], resolve),
		},
		{
			path: '/quiz/:id?',
			name: 'quiz',
			component: resolve => require(['@/components/quiz'], resolve),
		},
		{
			path: '/testdetails/:id?',
			name: 'testdetails',
			component: resolve => require(['@/components/testdetails'], resolve),
		},
		{
			path: '/prologue/:id?',
			name: 'prologue',
			component: resolve => require(['@/components/prologue'], resolve),
		},
		{
			path: '/',
			name: 'login',
			component: resolve => require(['@/components/login'], resolve),
		},
		{
			path: '/acrobatics',
			name: 'acrobatics',
			component: resolve => require(['@/components/acrobatics'], resolve),
		},
		{
			path: '/about',
			name: 'about',
			component: resolve => require(['@/components/about'], resolve),
		},
		{
			path: '/discuss/:id?',
			name: 'discuss',
			component: resolve => require(['@/components/discuss'], resolve),
		},
		{
			path: '/tradition',
			name: 'tradition',
			component: resolve => require(['@/components/tradition'], resolve),
		},
		{
			path: '/verbal',
			name: 'verbal',
			component: resolve => require(['@/components/verbal'], resolve),
		},
		{
			path: '/guide',
			name: 'guide',
			component: resolve => require(['@/components/guide'], resolve),
		},
		{
			path: '/eqtest',
			name: 'eqtest',
			component: resolve => require(['@/components/eqtest'], resolve),
		},
		{
			path: '/groupchat',
			name: 'groupchat',
			component: resolve => require(['@/components/groupchat'], resolve),
		},
		{
			path: '/upgrade',
			name: 'upgrade',
			component: resolve => require(['@/components/upgrade'], resolve),
		},
		{
			path: '/usinghelpo',
			name: 'usinghelpo',
			component: resolve => require(['@/components/usinghelpo'], resolve),
		},
		{
			path: '/usinghelpt',
			name: 'usinghelpt',
			component: resolve => require(['@/components/usinghelpt'], resolve),
		},
		{
			path: '/discussion',
			name: 'discussion',
			component: resolve => require(['@/components/discussion'], resolve),
		},
		{
			path: '/question',
			name: 'question',
			component: resolve => require(['@/components/question'], resolve),
		},
		{
			path: '/mine',
			name: 'mine',
			component: resolve => require(['@/components/mine'], resolve),
		},
		{
			path: '/response/:id?',
			name: 'response',
			component: resolve => require(['@/components/response'], resolve),
		},
		{
			path: '/myresponse',
			name: 'myresponse',
			component: resolve => require(['@/components/myresponse'], resolve),
		},
		{
			path: '/modification',
			name: 'modification',
			component: resolve => require(['@/components/modification'], resolve),
		},
		{
			path: '/personal',
			name: 'personal',
			component: resolve => require(['@/components/personal'], resolve),
		},
		{
			path: '/nickname',
			name: 'nickname',
			component: resolve => require(['@/components/nickname'], resolve),
		},
		{
			path: '/report/:id?',
			name: 'report',
			component: resolve => require(['@/components/report'], resolve),
		},
		{
			path: '/privacy',
			name: 'privacy',
			component: resolve => require(['@/components/privacy'], resolve),
		},
		{
			path: '/show',
			name: 'show',
			component: resolve => require(['@/components/show'], resolve),
		},
		{
			path: '/article',
			name: 'article',
			component: resolve => require(['@/components/article'], resolve),
		},
		{
			path: '/myquestions',
			name: 'myquestions',
			component: resolve => require(['@/components/myquestions'], resolve),
		},
		{
			path: '/comment',
			name: 'comment',
			component: resolve => require(['@/components/comment'], resolve),
		},
		{
			path: '/result/:id?/:point?/:now?/:total?',
			name: 'result',
			component: resolve => require(['@/components/result'], resolve),
		},
		{
			path: '/showtu/:id?',
			name: 'showtu',
			component: resolve => require(['@/components/showtu'], resolve),
		},
		{
			path: '/feedback',
			name: 'feedback',
			component: resolve => require(['@/components/feedback'], resolve),
		},
		{
			path: '/search',
			name: 'search',
			component: resolve => require(['@/components/search'], resolve),
		}
		
	]
})


